body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
@font-face {font-family: "iconfont";
  src: url('//at.alicdn.com/t/font_1505979_6h3zf2awl68.eot?t=1573696737270'); /* IE9 */
  src: url('//at.alicdn.com/t/font_1505979_6h3zf2awl68.eot?t=1573696737270#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2'),
  url('//at.alicdn.com/t/font_1505979_6h3zf2awl68.woff?t=1573696737270') format('woff'),
  url('//at.alicdn.com/t/font_1505979_6h3zf2awl68.ttf?t=1573696737270') format('truetype'), /* chrome, firefox, opera, Safari, Android, iOS 4.2+ */
  url('//at.alicdn.com/t/font_1505979_6h3zf2awl68.svg?t=1573696737270#iconfont') format('svg'); /* iOS 4.1- */
}

.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

